@import url(https://fonts.googleapis.com/css2?family=Sen:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Sen', sans-serif;
}

#bg-homepage{
  width: 100vw;
  height: 100vh
}

.title {
  color: white;
  font-size: 11vw;

}

.desc-title{
  color: #FF9A02;
  font-size: 2.2vw;
  width: 44%;
  font-weight: bold;
  margin-left:28%;
}
.title-container{
  position: absolute;
  top: 33vh;
  width: 100%;
  line-height: 1;
}
.nvbar{
    position: absolute;
    top: 0;
    width: 100vw;
    background-color: #00B7C282;
    height: 3vw;
    padding: 0;
    align-self: center;

}

.nvbar-content{
    width:100vw;
    color: white;
    font-size: 2vw;
    font-weight: 800;
}

